.footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.footercontent{
    background-color: white;
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 1px 1px rgb(175, 175, 175);
}
.footercontent .calculate{
    padding: 10px;
    color: white;
    letter-spacing: 1px;
    border-radius: 100px;
    background-color: #42B5E1;
    border: none;
    outline: none;
}
.footercontent .calculate:hover{
    background-color: rgb(128, 204, 255);
}

@media only screen and (max-width: 500px) {
    .footerinfo{
      font-size: 15px;
    }
}