.popup{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    background-color: rgba(128, 128, 128, 0.589);
    height: 100%;
    width: 100%;
}
.popupcontent{
    border-radius: 20px;
    background-color: white;
    padding: 20px;
}
.popupcontent h1{
    cursor: pointer;
    margin: -15px 5px 5px 0px;
    text-align: right;
}
.popupcontent textarea
{
    border-radius: 10px;
    padding: 10px;
    background-color: rgb(251, 251, 251);
}
.anotation{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.reactlogo{
    height: 25px;
    margin: 0px 5px;
}
.e404{
    padding: 50px;
    background-color: rgb(250, 250, 250);
}