.header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0px 0px 5px 1px rgb(216, 216, 216);
    padding: 10px 0px 10px 0px;
}
.header .logo{
    height: 40px;
}

@media only screen and (max-width: 500px) {
    .header h2{
      font-size: 20px;
    }
    .header .logo{
        height: 25px;
    }
  }