.boxcontain{
    margin-top: 15px;
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.boxcontain .textboxes{
    padding: 5px;
    margin: 0px 5px;
}
.boxcontain .textboxes .delete{
    text-align: right;
    margin-right: 10px;
    cursor: pointer;
}
.boxcontain textarea{
    border-radius: 10px;
    padding: 5px;
}
.addNew{
    margin-top: 15px;
    margin-left: 15px;
    cursor: pointer;
    border: 2px dashed rgb(104, 104, 104);
    border-radius: 10px;
    background-color: rgb(251, 251, 251);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 175px;
    width: 210px;
}
.addNew p{
    letter-spacing: 1px;
    color: rgb(98, 98, 98);
    margin: 10px;
}